//import { __ } from '@wordpress/i18n';
import { useState } from '@wordpress/element';
import { useSelect } from '@wordpress/data';
import { useBlockProps, InspectorControls } from '@wordpress/block-editor';
import 'bootstrap/dist/css/bootstrap.min.css';
import './editor.scss';
import { TextControl, SelectControl, PanelBody } from '@wordpress/components';

export default function Edit( { attributes, setAttributes } ) {
	const { columns } = attributes;
	const onChangeColumns = ( newColumns ) => {
		setAttributes( { columns: newColumns } );
	};

	const [ search, setSearch ] = useState( '' );
	const [ sortBy, setSortBy ] = useState( 'address' );
	const [ sortOrder, setSortOrder ] = useState( 'asc' );

	const posts = useSelect( ( select ) =>
		select( 'core' ).getEntityRecords( 'postType', 'gas-station', {
			per_page: -1,
		} )
	);

	const safePosts = Array.isArray( posts ) ? posts : [];

	const filteredPosts = safePosts.filter( ( post ) => {
		const address = String(
			post.meta?.[ 'gas-station_address' ] || ''
		).toLowerCase();

		return address.includes( search.toLowerCase() );
	} );

	const sortedPosts = [ ...filteredPosts ].sort( ( a, b ) => {
		let valueA;
		let valueB;

		switch ( sortBy ) {
			case 'id':
				valueA = Number( a.meta?.[ 'gas-station_object_id' ] || 0 );
				valueB = Number( b.meta?.[ 'gas-station_object_id' ] || 0 );
				break;

			case 'distance':
				// пример: используем X как "distance"
				// valueA = Number( a.meta?.['gas-station_geometry_x'] || 0 );
				// valueB = Number( b.meta?.['gas-station_geometry_x'] || 0 );
				break;

			case 'address':
			default:
				valueA = String(
					a.meta?.[ 'gas-station_address' ] || ''
				).toLowerCase();
				valueB = String(
					b.meta?.[ 'gas-station_address' ] || ''
				).toLowerCase();
		}

		if ( valueA < valueB ) return sortOrder === 'asc' ? -1 : 1;
		if ( valueA > valueB ) return sortOrder === 'asc' ? 1 : -1;
		return 0;
	} );

	return (
		<>
			<InspectorControls>
				<PanelBody title="Layout">
					<SelectControl
						label="Columns"
						value={ columns }
						options={ [
							{ label: '1 column', value: 12 },
							{ label: '2 columns', value: 6 },
							{ label: '3 columns', value: 4 },
							{ label: '4 columns', value: 3 },
							{ label: '6 columns', value: 2 },
						] }
						onChange={ onChangeColumns }
					/>
				</PanelBody>
			</InspectorControls>
			<div
				{ ...useBlockProps( {
					className: `container border border-info rounded-1 bg-light`,
				} ) }
			>
				{ /* --- Controls --- */ }
				<div className="border-bottom border-info p-1">
					<TextControl
						label="Search by address"
						value={ search }
						onChange={ setSearch }
						placeholder="Enter address..."
					/>
					<SelectControl
						label="Sort by"
						value={ sortBy }
						options={ [
							{ label: 'Address', value: 'address' },
							// { label: 'Distance', value: 'distance' },
							{ label: 'ID', value: 'id' },
						] }
						onChange={ setSortBy }
					/>

					<SelectControl
						label="Order"
						value={ sortOrder }
						options={ [
							{ label: 'Ascending', value: 'asc' },
							{ label: 'Descending', value: 'desc' },
						] }
						onChange={ setSortOrder }
					/>
				</div>

				{ /* --- Content --- */ }
				{ ! posts && <p>Loading…</p> }

				{ posts && posts.length === 0 && (
					<p>No posts found for this post type.</p>
				) }
				{ posts && posts.length > 0 && (
					<div className="gas-stations-grid row w-100 m-0 d-flex flex-wrap">
						{ sortedPosts.map( ( post ) => {
							const meta = post.meta || {};
							return (
								<div
									key={ post.id }
									className={ `col-${ columns } p-1` }
								>
									<div className="card border-info mb-3 p-0">
										{ /* <img src="..." class="card-img-top" alt="..."> */ }
										<div
											className="card-header"
											dangerouslySetInnerHTML={ {
												__html: post.title.rendered,
											} }
										/>
										<div className="card-body">
											{ meta[ 'gas-station_address' ] && (
												<h5 className="card-title">
													<strong>Address:</strong>{ ' ' }
													{
														meta[
															'gas-station_address'
														]
													}
												</h5>
											) }
											{ meta[
												'gas-station_geometry_x'
											] && (
												<p className="card-text">
													<strong>X:</strong>{ ' ' }
													{
														meta[
															'gas-station_geometry_x'
														]
													}
												</p>
											) }
											{ meta[
												'gas-station_geometry_y'
											] && (
												<p className="card-text">
													<strong>Y:</strong>{ ' ' }
													{
														meta[
															'gas-station_geometry_y'
														]
													}
												</p>
											) }
										</div>
									</div>
								</div>
							);
						} ) }
					</div>
				) }
			</div>
		</>
	);
}
